<template>
  <div class="txt-to-pdf">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>TXT 转 PDF</span>
          <el-icon><Document /></el-icon>
        </div>
      </template>

      <el-form :model="form" label-width="120px">
        <el-form-item label="选择TXT文件" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".txt"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">将TXT文件拖到此处，或<em>点击上传</em></div>
            <template #tip>
              <div class="el-upload__tip">只能上传 .txt 文件，且不超过100MB</div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="文本编码">
          <el-select v-model="form.encoding">
            <el-option label="UTF-8" value="utf-8" />
            <el-option label="GBK" value="gbk" />
            <el-option label="GB2312" value="gb2312" />
            <el-option label="ISO-8859-1" value="iso-8859-1" />
          </el-select>
        </el-form-item>

        <el-form-item label="纸张大小">
          <el-select v-model="form.pageSize">
            <el-option label="A4" value="A4" />
            <el-option label="Letter" value="Letter" />
            <el-option label="Legal" value="Legal" />
          </el-select>
        </el-form-item>

        <el-form-item label="方向">
          <el-select v-model="form.orientation">
            <el-option label="纵向 (portrait)" value="portrait" />
            <el-option label="横向 (landscape)" value="landscape" />
          </el-select>
        </el-form-item>

        <el-form-item label="页边距">
          <el-input v-model="form.margins" placeholder="left,top,right,bottom (pt)" />
          <div class="form-tip">默认 36,36,36,36（单位：pt）</div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" :loading="loading" :disabled="!selectedFile" @click="handleSubmit">
            {{ loading ? '处理中...' : '开始转换' }}
          </el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>

      <div v-if="result" class="result-section">
        <el-divider>处理结果</el-divider>
        <el-alert :title="result.msg" type="success" :closable="false" show-icon />
        <div class="result-info">
          <p><strong>任务ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>输出文件:</strong> {{ result.data.convertedFile }}</p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            下载PDF
          </el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Document, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { downloadFile } from '@/utils/download'

const uploadRef = ref()
const loading = ref(false)
const selectedFile = ref(null)
const result = ref(null)

const form = reactive({
  encoding: 'utf-8',
  pageSize: 'A4',
  orientation: 'portrait',
  margins: '36,36,36,36'
})

const handleFileChange = (file) => {
  const raw = file.raw
  const isTxt = /\.txt$/i.test(raw.name)
  if (!isTxt) {
    ElMessage.error('仅支持 .txt 文件')
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = raw
}

const beforeUpload = (file) => {
  const isTxt = /\.txt$/i.test(file.name)
  if (!isTxt) {
    ElMessage.error('仅支持 .txt 文件')
    return false
  }
  return false
}

const handleSubmit = async () => {
  if (!selectedFile.value) {
    ElMessage.warning('请先选择TXT文件')
    return
  }
  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('encoding', form.encoding)
    formData.append('pageSize', form.pageSize)
    formData.append('orientation', form.orientation)
    formData.append('margins', form.margins)

    const response = await pdfApi.fromTxt(formData)
    result.value = response
    ElMessage.success('转换成功')
  } catch (e) {
    console.error(e)
  } finally {
    loading.value = false
  }
}

const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = result.value.data.convertedFile || 'converted.pdf'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('下载已开始')
    } catch (error) {
      console.error('下载失败:', error)
      ElMessage.error('下载失败，请重试')
    }
  } else {
    ElMessage.warning('没有可下载的文件')
  }
}

const resetForm = () => {
  form.encoding = 'utf-8'
  form.pageSize = 'A4'
  form.orientation = 'portrait'
  form.margins = '36,36,36,36'
  selectedFile.value = null
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.txt-to-pdf { max-width: 800px; margin: 0 auto; }
.card-header { display: flex; justify-content: space-between; align-items: center; font-size: 18px; font-weight: bold; }
.form-tip { font-size: 12px; color: #909399; margin-top: 5px; }
.result-section { margin-top: 20px; }
.result-info { margin-top: 15px; padding: 15px; background-color: #f5f7fa; border-radius: 4px; }
</style>
